<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <!--  :class="{'active':isChoose}" -->
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input @input="change($event)" style="width: 203px" v-model="searchList.productName" clearable placeholder="商品名称"></el-input>
          </el-form-item>
          <el-form-item label="订单编号：">
            <el-input @input="change($event)" style="width: 203px" v-model="searchList.orderNo" clearable placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="交易流水号：">
            <el-input @input="change($event)" style="width: 203px" v-model="searchList.payNumber" clearable placeholder="交易流水号"></el-input>
          </el-form-item>
          <el-form-item label="下单时间：">
            <el-date-picker v-model="applicationTime"
              type="daterange"
              value-format="yyyy-MM-dd HH:mm:ss"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="收货人手机号：">
            <el-input @input="change($event)" style="width: 203px" v-model="searchList.receiverPhone" clearable placeholder="收货人手机号"></el-input>
          </el-form-item>
          <el-form-item label="收货人：">
            <el-input @input="change($event)" style="width: 203px" v-model="searchList.receiverName" clearable placeholder="收货人"></el-input>
          </el-form-item>
          <el-form-item label="配送方式：">
            <el-select v-model="searchList.deliveryType" placeholder="配送方式">
              <el-option label="物流配送" value="0"></el-option>
              <el-option label="上门自提" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="维权状态：">
            <el-select v-model="searchList.postSaleStatus" placeholder="维权状态">
              <el-option label="无售后" value="0"></el-option>
              <el-option label="有售后" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单来源：">
            <el-select v-model="searchList.orderType" placeholder="订单来源">
              <el-option label="H5订单" value="0"></el-option>
              <el-option label="小程序订单" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:20px" shadow="never">
      <el-tabs v-model="searchList.statusType" @tab-click="getData">
        <el-tab-pane label="全部" name="10"></el-tab-pane>
        <el-tab-pane label="待付款" name="0"></el-tab-pane>
        <el-tab-pane label="待发货" name="1"></el-tab-pane>
        <el-tab-pane label="待收货" name="2"></el-tab-pane>
        <el-tab-pane label="交易成功" name="3"></el-tab-pane>
        <el-tab-pane label="已关闭" name="4"></el-tab-pane>
      </el-tabs>
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="primary" @click="exportOrder">导出订单</el-button>
          <el-button type="primary" @click="exportSendOrder">导出发货单</el-button>
          <el-button @click="sendItemZT({orderId:''})" v-show="searchList.statusType == 10 || searchList.statusType == 1">自提订单发货</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table ref="multipleTable" :data="list" resizable border style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column
            :selectable='selectable'
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table :data="props.row.orderItemList" style="width: 100%">
                <el-table-column prop="productName" label="商品名称" width="300"></el-table-column>
                <el-table-column label="是否售后" width="180">
                  <template slot-scope="scope">
                    {{scope.row.postSaleStatus == 0 ? '无售后' : '有售后'}}
                  </template>
                </el-table-column>
                <el-table-column label="活动类型" prop="activityType" width="180">
                  <template slot-scope="scope">
                    <div v-if="scope.row.activityType == 0">未参与</div>
                    <div v-if="scope.row.activityType == 1">限时折扣</div>
                    <div v-if="scope.row.activityType == 2">砍价</div>
                  </template>
                </el-table-column>
                <el-table-column prop="productCode" label="商品编码" width="180"></el-table-column>
                <el-table-column label="规格" width="240">
                  <template slot-scope="scope">{{scope.row.skuName ? scope.row.skuName : '-'}}</template>
                </el-table-column>
                <el-table-column prop="productCount" label="商品数量" width="140"></el-table-column>
                <el-table-column prop="originalPrice" label="商品金额" width="180"></el-table-column>
                <el-table-column label="优惠金额" width="180">
                  <template slot-scope="scope">
                    <div>
                      <div>{{scope.row.couponDiscountPrice ? scope.row.couponDiscountPrice + '优惠券优惠金额' : ''}}</div>
                      <div>{{scope.row.selfDiscountPrice ? scope.row.selfDiscountPrice + '自定义优惠金额' : ''}}</div>
                      <div>{{scope.row.promotionDiscountPrice ? scope.row.promotionDiscountPrice + '促销优惠金额，满减，满折' : ''}}</div>
                      <div v-if="scope.row.activityDiscountPrice">{{scope.row.activityDiscountPrice}} {{scope.row.activityType == 1 ? '限时折扣' : ''}} {{scope.row.activityType == 2 ? '砍价' : ''}}</div>
                      <div>{{scope.row.vipDiscountPrice ? scope.row.vipDiscountPrice + '会员优惠金额' : ''}}</div>
                      <div v-if="
                        !scope.row.couponDiscountPrice &&
                        !scope.row.selfDiscountPrice && 
                        !scope.row.promotionDiscountPrice && 
                        !scope.row.activityDiscountPrice && 
                        !scope.row.vipDiscountPrice
                      "
                      > - </div>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column prop="payPrice" label="商品总金额" width="180"></el-table-column>
              </el-table>
            </template>
          </el-table-column>
          <el-table-column label="订单编号" prop="orderNo" width="240"></el-table-column>
          <el-table-column label="配送方式" width="140">
            <template slot-scope="scope">
              {{scope.row.deliveryType == 0 ? '物流配送' : '上门自提'}}
            </template>
          </el-table-column>
          <el-table-column label="下单时间" prop="createTime" width="240"></el-table-column>
          <el-table-column label="收货人" prop="receiverName" width="140"></el-table-column>
          <el-table-column label="收货人手机号" prop="receiverPhone" width="180"></el-table-column>
          <el-table-column label="收货地址" prop="address" width="300" show-overflow-tooltip></el-table-column>
          <el-table-column label="商品数量" prop="goodCount" width="100"></el-table-column>
          <el-table-column label="订单金额" prop="payAmount" width="200"></el-table-column>
          <el-table-column label="营销类型" prop="activityType" width="180">
            <template slot-scope="scope">
              <div v-if="scope.row.activityType == 0">未参与</div>
              <div v-if="scope.row.activityType == 1">限时折扣</div>
              <div v-if="scope.row.activityType == 2">砍价</div>
            </template>
          </el-table-column>
          <el-table-column label="支付方式" width="180">
            <template slot-scope="scope">
              <div v-if="scope.row.payType == 0">微信代销</div>
              <div v-if="scope.row.payType == 1">微信自有支付</div>
              <div v-if="scope.row.payType == 2">小程序支付</div>
              <div v-if="scope.row.payType == 3">0元支付</div>
            </template>
          </el-table-column>
          <el-table-column label="订单状态" width="140">
            <template slot-scope="scope">
              <div v-if="scope.row.status == 0" style="color:#E6A23C;">待付款</div>
              <div v-if="scope.row.status == 1" style="color:#E6A23C;">待发货</div>
              <div v-if="scope.row.status == 2" style="color:#67C23A;">已发货</div>
              <div v-if="scope.row.status == 3" style="color:#67C23A;">已完成</div>
              <div v-if="scope.row.status == 4" style="color:#F56C6C;">已关闭</div>
            </template>
          </el-table-column>
          <el-table-column label="买家留言" width="300" show-overflow-tooltip>
            <template slot-scope="scope">{{scope.row.userRemark ? scope.row.userRemark : '-'}}</template>
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="$router.push({path:'/order/orderList/orderinfo',query:{orderId:scope.row.orderId}})">查看</el-button>
              <el-button type="text" size="small" @click="sendItem(scope.row)" v-if="scope.row.deliveryType == 0 && scope.row.status == 1">去发货</el-button>
              <el-button type="text" size="small" @click="sendItemZT(scope.row)" v-if="scope.row.deliveryType == 1 && scope.row.status == 1">去发货</el-button>
              <el-button type="text" size="small" @click="editItem(scope.row)" v-if="scope.row.deliveryType == 0 && scope.row.status == 1">修改收货信息</el-button>
              <el-button type="text" size="small" @click="remarkItem(scope.row)">订单备注</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-between" style="padding-top:20px;">
          <div>
            <el-button v-show="searchList.statusType == 10 || searchList.statusType == 1" type="primary" @click="sendAll">批量发货</el-button>
          </div>
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 订单备注弹出框 -->
    <el-dialog title="订单备注" :visible.sync="remarkShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-collapse v-model="remarkOpenList" :data-list="remarkOpenList">
        <el-collapse-item v-for="(item,index) in remarkList" :key="index" :title="item.createTime" :name="index">
          <div>{{item.remark}}</div>
        </el-collapse-item>
      </el-collapse>
      <div style="margin-top:20px;" v-show="!remarkInputShow">
        <el-button type="primary" :disabled="remarkList.length >= 5" @click="showRemarkInput">添加备注</el-button>
        <span style="color:#909399;">一个订单最多只可添加5条备注</span>
      </div>
      <div v-if="remarkInputShow" style="margin-top:20px;">
        <div>
          <el-input type="textarea" placeholder="请输入备注" v-model.trim="remarkForm.remark" maxlength="100" show-word-limit></el-input>
        </div>
        <div>
          <el-button type="text" style="color:#909399;" @click="remarkInputShow = false">取 消</el-button>
          <el-button type="text" style="margin-left:20px;" :loading="addRemarkLoading" @click="subRemark">确 定</el-button>
        </div>
      </div>
    </el-dialog>
    <!-- 自提发货弹出框 -->
    <el-dialog title="提货确认" :visible.sync="ztSendShow" width="50%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form :model="sendForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="提货码：" required>
          <el-input style="width:400px;" v-model.trim="sendForm.deliveryCode"></el-input>
        </el-form-item>
        <el-form-item label="温馨提示：">
          <div style="color:#F56C6C;">自提订单确认提货后，订单状态更新至已收货。</div>
        </el-form-item>
        <el-form-item>
          <el-button @click="ztSendShow = false">取 消</el-button>
          <el-button type="primary" :loading="ztSendLoading" @click="sendSelf">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 物流发货弹出框 -->
    <el-dialog title="快递信息" :visible.sync="wlSendShow" width="50%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form :model="sendForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="快递名称：" required>
          <el-select v-model="sendForm.expressCode" placeholder="请选择物流公司">
            <el-option v-for="(item,index) in expressList" :key="index" :label="item.expressName" :value="item.expressCode"></el-option>
          </el-select>
          <el-button @click="getExpressList" style="margin:0 20px">刷新</el-button>
          <el-button @click="newPage1" type="primary">添加物流</el-button>
        </el-form-item>
        <el-form-item label="快递单号：" required>
          <el-input style="width:400px;" v-model.trim="sendForm.expressNo" placeholder="请输入快递单号"></el-input>
        </el-form-item>
        <el-form-item label="温馨提示：">
          <div style="color:#F56C6C;">发货时请留意商品是否申请售后，申请售后的商品系统默认不出库处理。</div>
        </el-form-item>
        <el-form-item>
          <el-button @click="wlSendShow = false">取 消</el-button>
          <el-button type="primary" :loading="wlSendLoading" @click="sendPost">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 批量发货弹出框 -->
    <el-dialog title="批量发货" :visible.sync="sendAllShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-table :data="sendAllList" border style="width: 100%">
        <el-table-column prop="orderNo" label="订单号"></el-table-column>
        <el-table-column prop="receiverName" label="收货人"></el-table-column>
        <el-table-column prop="receiverPhone" label="手机号"></el-table-column>
        <el-table-column label="物流公司">
          <template slot-scope="scope">
            <el-select v-model="scope.row.expressCode" placeholder="请选择物流公司">
              <el-option v-for="(item,index) in expressList" :key="index" :label="item.expressName" :value="item.expressCode"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="物流单号">
          <template slot-scope="scope">
            <el-input style="width:100%;" v-model.trim="scope.row.expressNo" placeholder="请输入快递单号"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="deleteMu(scope.$index)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sendAllShow = false">取 消</el-button>
        <el-button type="primary" :loading="sendAllLoading" @click="sendAllFun">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 更改收货地址弹出框 -->
    <el-dialog title="修改收货信息" :visible.sync="addressShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form ref="form" :model="addressForm" label-width="120px">
        <el-form-item label="收货人姓名" required>
          <el-input v-model.trim="addressForm.receiverName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="收货人联系电话" required>
          <el-input v-model.trim="addressForm.receiverPhone" maxlength="11" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="收货地址：" required>
          <div class="flex-row-start">
            <el-form-item style="margin-right:10px;">
              <el-select @change="address1" value-key="code" clearable v-model="addressForm.province" placeholder="请选择省">
                <el-option v-for="item in addressList1" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="margin-right:10px;">
              <el-select @change="address2" value-key="code" clearable v-model="addressForm.city" placeholder="请选择市">
                <el-option v-for="item in addressList2" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-select v-model="addressForm.district" clearable value-key="code" placeholder="请选择区">
                <el-option v-for="item in addressList3" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form-item>
        <el-form-item label="详细地址" required>
          <el-input type="textarea" placeholder="请输入内容" v-model="addressForm.receiverAddress" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressShow = false">取 消</el-button>
        <el-button type="primary" :loading="addressLoading" @click="subAddress">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  findAll ,
  expressOrder ,
  findOrderRemark ,
  addOrderRemark ,
  updateAddress
} from '@/api/order/orderList'
import { getAddressLevel } from '@/api/shop/createShop'
import { findExpressCompanys } from '@/api/logistics/expressTemplate'
export default {
  data(){
    return{
      searchList:{                      // 搜索栏数据
        statusType:'10',
        pageNum:1,
        pageSize:5
      },
      list:[],                          // 订单列表
      listTotal:0,                      // 列表总数
      applicationTime: '',              // 搜索筛选时间
      pickerOptions: {                  // 时间数据
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      remarkShow:false,                 // 订单备注弹出框
      remarkList:[],                    // 订单备注列表
      remarkForm:{},
      remarkOpenList:[],                // 备注列表
      addRemarkLoading:false,           // 提交备注loading
      remarkInputShow:false,          // 是否展示弹出框的输入框
      orderRemark:'',                 // 弹出框输入框的内容
      multipleSelection: [],          // 已选中的数据
      wlSendShow:false,               // 物流发货弹出框
      wlSendLoading:false,            // 物流发货loading
      expressList:[],                 // 发货物流地址列表
      sendForm:{},                    // 发货数据 => 物流 || 自提
      ztSendShow:false,               // 物流发货弹出框
      ztSendShow:false,               // 自提发货弹出框
      ztSendLoading:false,            // 自提发货loading
      sendAllShow:false,              // 批量发货弹出层
      sendAllList:[],
      sendAllLoading:false,
      addressShow:false,               // 修改收货信息弹出框
      addressLoading:false,
      addressForm:{},
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.sendForm.shopId = this.shop.shopId
    if(this.$route.query.statusType) this.searchList.statusType = this.$route.query.statusType+''
    if(this.$route.query.receiverPhone) this.searchList.receiverPhone = this.$route.query.receiverPhone+'' || ''
    this.getData();
  },
  methods:{
    change(e){
      this.$forceUpdate()
    },
    exportSendOrder(){
      this.$confirm('确认导出发货单到excel?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let status = this.searchList.statusType;
        if(this.searchList.statusType == '10') status = ''
        let str = `shopId=${this.shop.shopId}&productName=${this.searchList.productName || ''}&orderNo=${this.searchList.orderNo || ''}&payNumber=${this.searchList.payNumber || ''}&postSaleStatus=${this.searchList.postSaleStatus || ''}&deliveryType=${this.searchList.deliveryType || ''}&orderType=${this.searchList.orderType || ''}&status=${status}&receiverName=${this.searchList.receiverName || ''}&receiverPhone=${this.searchList.receiverPhone || ''}&startCreateTime=${this.applicationTime[0] || ''}&endCreateTime=${this.applicationTime[1] || ''}`
        window.open(
          `/order/business/order/exportDeliverOrderInfo?${str}`
        )
      }).catch(() => {});
    },
    exportOrder(){
      this.$confirm('确认导出订单到excel?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let status = this.searchList.statusType;
        if(this.searchList.statusType == '10') status = ''
        let str = `shopId=${this.shop.shopId}&productName=${this.searchList.productName || ''}&orderNo=${this.searchList.orderNo || ''}&payNumber=${this.searchList.payNumber || ''}&postSaleStatus=${this.searchList.postSaleStatus || ''}&deliveryType=${this.searchList.deliveryType || ''}&orderType=${this.searchList.orderType || ''}&status=${status}&receiverName=${this.searchList.receiverName || ''}&receiverPhone=${this.searchList.receiverPhone || ''}&startCreateTime=${this.applicationTime[0] || ''}&endCreateTime=${this.applicationTime[1] || ''}`
        window.open(
          `/order/business/order/export?${str}`
        )
      }).catch(() => {});
    },
    // 列表 - 获取数据
    getData(){
      console.log('applicationTime',this.applicationTime)
      this.searchList.startCreateTime = this.applicationTime[0] || ''
      this.searchList.endCreateTime = this.applicationTime[1] || ''
      this.searchList.status = this.searchList.statusType;
      if(this.searchList.statusType == '10') this.searchList.status = ''
      console.log('请求的数据',this.searchList)
      findAll(this.searchList).then(res=>{
        console.log('订单列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 自提 - 去发货
    sendItemZT(e){
      this.sendForm.orderId = e.orderId;
      this.ztSendShow = true;
    },
    // 修改收货地址
    editItem(e){
      console.log('修改收货地址e',e)
      this.getAddressInput(1,'')
      this.getAddressInput(2,e.receiverProvinceCode)
      this.getAddressInput(3,e.receiverCityCode)
      let province = {
        code:e.receiverProvinceCode,
        name:e.receiverProvinceName,
      }
      let city = {
        code:e.receiverCityCode,
        name:e.receiverCityName,
      }
      let district = {
        code:e.receiverDistrictCode,
        name:e.receiverDistrictName,
      }
      let obj = {
        id:e.orderId,
        receiverAddress:e.receiverAddress,
        receiverName:e.receiverName,
        receiverPhone:e.receiverPhone,
        province,
        city,
        district
      }
      this.addressForm = obj
      this.addressShow = true;
    },
    subAddress(){
      console.log('this.addressForm',this.addressForm)
      if(!this.addressForm.receiverName){
        this.$message.error('请填写收货人姓名');
        return;
      }
      if(!this.addressForm.receiverPhone){
        this.$message.error('请填写收货人联系电话');
        return;
      }
      if(!this.addressForm.province){
        this.$message.error('请选择省');
        return;
      }
      this.addressForm.receiverProvinceName = this.addressForm.province.name
      this.addressForm.receiverProvinceCode = this.addressForm.province.code
      if(!this.addressForm.city){
        this.$message.error('请选择市');
        return;
      }
      this.addressForm.receiverCityName = this.addressForm.city.name
      this.addressForm.receiverCityCode = this.addressForm.city.code
      if(!this.addressForm.district){
        this.$message.error('请选择区');
        return;
      }
      this.addressForm.receiverDistrictName = this.addressForm.district.name
      this.addressForm.receiverDistrictCode = this.addressForm.district.code
      if(!this.addressForm.receiverAddress){
        this.$message.error('请填写收货人详细地址');
        return;
      }
      this.addressLoading = true;
      updateAddress(this.addressForm).then(res=>{
        console.log('修改地址')
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.addressShow = false;
          setTimeout(()=>{
            this.addressLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.addressLoading = false;
        }
      })
    },
    // 批量发货弹出框 - 删除
    deleteMu(i){
      console.log('i',i)
      this.sendAllList.splice(i,1)
      if(this.sendAllList.length == 0) this.sendAllShow = false;
    },
    // 批量发货
    sendAllFun(){
      console.log('this.sendAllList',this.sendAllList)
      let errArr = this.sendAllList.filter( e => e.expressCode == '' || e.expressNo == '');
      console.log('errArr',errArr)
      if(errArr.length > 0){
        this.$message.error('请填完数据后再试');
        return;
      }
      this.sendAllLoading = true;
      expressOrder(this.sendAllList).then(res=>{
        console.log('发货',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.sendAllShow = false;
          setTimeout(()=>{
            this.sendAllLoading = false;
          },1000)
        }else{
          this.sendAllLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 批量发货
    sendAll(e){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择订单后再试');
        return;
      }
      this.getExpressList()
      this.multipleSelection.forEach(e => {
        let obj = {
          orderId:e.orderId,
          orderNo:e.orderNo,
          receiverName:e.receiverName,
          receiverPhone:e.receiverPhone,
          expressCode:'',
          expressNo:'',
        }
        this.sendAllList.push(obj)
      })
      this.sendAllShow = true;
    },
    // 列表 - 备注
    remarkItem(e){
      this.remarkShow = true;
      this.getRemarkList(e.orderId)
      this.remarkForm.orderId = e.orderId
      this.remarkForm.orderStatus = e.status
    },
    // 获取备注列表
    getRemarkList(id){
      findOrderRemark(id).then(res => {
        console.log('备注列表',res)
        if(res.code == 200){
          this.remarkList = res.data;
          this.remarkOpenList = []
          this.remarkList.forEach((e,i) => this.remarkOpenList.push(i))
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 备注弹出框 - 添加备注按钮
    showRemarkInput(){
      this.remarkInputShow = true;
    },
    // 备注弹出框 - 添加备注
    subRemark(){
      console.log('orderRemark',this.remarkForm.remark)
      if(!this.remarkForm.remark){
        this.$message.error('请输入备注内容');
        return;
      }
      this.remarkForm.remarkType = 0
      this.remarkForm.createUser = this.user.userId
      console.log('添加备注请求的参数',this.remarkForm)
      this.addRemarkLoading = true;
      addOrderRemark(this.remarkForm).then(res=>{
        console.log('添加备注',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getRemarkList(this.remarkForm.orderId)
          this.remarkForm.remark = '';
          this.remarkInputShow = false;
          setTimeout(()=>{
            this.addRemarkLoading = false;
          },1000)
        }else{
          this.addRemarkLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 获取物流数据
    getExpressList(){
      findExpressCompanys(this.shop.shopId).then(res => {
        console.log('物流列表',res)
        this.expressList = res.data
      })
    },
    // 跳转添加物流页面
    newPage1(){
      let details = this.$router.resolve({
        name: "快递模板",
      });
      window.open(details.href, "_blank")
    },
    // 列表 - 物流 - 去发货
    sendItem(e){
      this.getExpressList()
      this.sendForm.orderId = e.orderId;
      this.wlSendShow = true;
    },
    // 弹出框 - 发货 - 物流
    sendPost(){
      if(!this.sendForm.expressCode){
        this.$message.error('请选择物流公司');
        return;
      }
      if(!this.sendForm.expressNo){
        this.$message.error('请输入快递单号');
        return;
      }
      console.log('sendForm',this.sendForm)
      this.wlSendLoading = true;
      let arr = []
      arr.push(this.sendForm)
      expressOrder(arr).then(res=>{
        console.log('发货',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.wlSendShow = false;
          setTimeout(()=>{
            this.wlSendLoading = false;
          },1000)
        }else{
          this.wlSendLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 弹出框 - 发货 - 自提
    sendSelf(){
      if(!this.sendForm.deliveryCode){
        this.$message.error('请输入提货码');
        return;
      }
      console.log('sendForm',this.sendForm)
      this.ztSendLoading = true;
      let arr = []
      arr.push(this.sendForm)
      expressOrder(arr).then(res=>{
        console.log('发货',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.ztSendShow = false;
          setTimeout(()=>{
            this.ztSendLoading = false;
          },1000)
        }else{
          this.ztSendLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    selectable(row,index) {
      return row.deliveryType == 0 && row.status == 1
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.productName = ''
      this.searchList.orderNo = ''
      this.searchList.payNumber = ''
      this.applicationTime = []
      this.searchList.startCreateTime = ''
      this.searchList.endCreateTime = ''
      this.searchList.receiverPhone = ''
      this.searchList.receiverName = ''
      this.searchList.deliveryType = ''
      this.searchList.postSaleStatus = ''
      this.searchList.orderType = ''
      this.getData()
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      console.log('执行了1',val)
      this.addressList2 = [];
      if(this.addressForm.city) this.addressForm.city = '';
      this.addressList3 = [];
      if(this.addressForm.district) this.addressForm.district = '';
      this.getAddressInput(2,val.code);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      console.log('执行了2',val)
      this.addressList3 = [];
      if(this.addressForm.district) this.addressForm.district = '';
      this.getAddressInput(3,val.code);
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log(`${id1}级下拉获取下拉框地址`,res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
  .active {
    height: 70px;
  }
</style>